@import './_global.scss';

* {
 margin: 0;
 padding: 0;
}

h1, h2, h3, p, body, ul {
 margin: 0;
 padding: 0;
}

img {
 vertical-align: bottom;
 margin: 0;
}

li {
 list-style: none;
}

a {
 text-decoration: none;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
 outline: none;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
 -webkit-appearance: none;
 margin: 0;
}

* {
 box-sizing: border-box;
}

html, body, #root {
 width: 100%;
 height: 100%;
}
